import React from 'react';
import { Link } from 'react-router-dom';

const Home: React.FC = () => {
  return (
    <div className="home-container">
      <div className="hero-section">
        <h1>Blood Donation Saves Lives</h1>
        <p className="hero-text">
          Every donation can save up to 3 lives. Join our community of donors and make a difference today.
        </p>
        <div className="cta-buttons">
          <Link to="/register" className="cta-button primary">Become a Donor</Link>
          <Link to="/compatibility" className="cta-button secondary">Check Compatibility</Link>
        </div>
      </div>

      <div className="info-section">
        <div className="info-card">
          <div className="info-icon"></div>
          <h2>Why Donate?</h2>
          <p>
            Blood cannot be manufactured  it can only come from generous donors. 
            Blood is needed every two seconds for emergency treatments, surgeries, and more.
          </p>
        </div>

        <div className="info-card">
          <div className="info-icon"></div>
          <h2>Am I Eligible?</h2>
          <p>
            Most people in good health, who are at least 17 years old, and weigh at least 110 pounds, 
            can donate blood. Use our compatibility checker to learn more about your blood type.
          </p>
        </div>

        <div className="info-card">
          <div className="info-icon"></div>
          <h2>Regional Impact</h2>
          <p>
            Different regions have different blood type needs. Check our regional distribution 
            map to see where your donation could make the biggest impact.
          </p>
        </div>
      </div>

      <div className="stats-section">
        <h2>Blood Donation Facts</h2>
        <div className="stats-container">
          <div className="stat-item">
            <div className="stat-number">38%</div>
            <div className="stat-text">of the population is eligible to donate blood</div>
          </div>
          <div className="stat-item">
            <div className="stat-number">3</div>
            <div className="stat-text">lives saved with each donation</div>
          </div>
          <div className="stat-item">
            <div className="stat-number">1 in 7</div>
            <div className="stat-text">people entering a hospital need blood</div>
          </div>
          <div className="stat-item">
            <div className="stat-number">56</div>
            <div className="stat-text">days before you can donate whole blood again</div>
          </div>
        </div>
      </div>

      <div className="action-section">
        <h2>Ready to Make a Difference?</h2>
        <p>Join our blood donor community today. Track your donations, set goals, and save lives.</p>
        <div className="action-buttons">
          <Link to="/register" className="action-button">Sign Up Now</Link>
          <Link to="/login" className="action-button outline">Log In</Link>
        </div>
      </div>
    </div>
  );
};

export default Home; 